import OktaAuth from '@okta/okta-auth-js';
import authConfig from './auth_config.json';
var getAuthProvider = function (provider) {
    var okta = authConfig.okta, auth0 = authConfig.auth0, azure = authConfig.azure;
    if (provider === 'auth0') {
        return {
            domain: auth0.domain,
            clientId: auth0.clientId,
            redirectUri: 'http://127.0.0.1:3000',
            scopes: ['openid', 'profile', 'email'],
        };
    }
    if (provider === 'okta') {
        return new OktaAuth({
            clientId: okta.clientId,
            issuer: okta.domain,
            redirectUri: 'http://127.0.0.1:3000',
            scopes: ['openid', 'profile', 'email'],
            pkce: true,
        });
    }
    if (provider === 'azure') {
        return {
            clientId: azure.clientId,
            authority: "https://login.microsoftonline.com/".concat(azure.tenantId),
            redirectUri: azure.redirectUri,
            scopes: ['openid', 'profile', 'email'],
        };
    }
    throw new Error("Unknown provider: ".concat(provider));
};
export default getAuthProvider;
